@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

* {
    box-sizing: border-box;
  }
  
  input {
    all: unset;
    box-sizing: border-box;
    appearance: none;
  }
 
  button {
    background-color: white;
    color: black;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }
  
  form {
    width: 100%;
  }
  
  .nweet-input::placeholder {
    color: white;
    opacity: 0.9;
  }
  
  .container {
    width: 100%;
    max-width: 320px;
    display: flex;
    flex-direction: column;
  }
  

  
   
 
  /* Nwitter Factory */
  

  
  .nweet {
    margin-bottom: 20px;
    background-color: white;
    width: 100%;
    max-width: 320px;
    padding: 20px;
    border-radius: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    color: rgba(0, 0, 0, 0.8);
  }
  
  .nweet h4 {
    font-size: 14px;
  }
  
  .nweet img {
    right: -10px;
    top: 20px;
    position: absolute;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-top: 10px;
  }
  
  .nweet__actions {
    position: absolute;
    right: 10px;
    top: 20px;
  }
  
  .nweet__actions span {
    cursor: pointer;
  }
  
  .nweet__actions span:first-child {
    margin-right: 10px;
  }
  
  